<template>
  <div class="service">
    <div class="service-title">
      <div>说明</div>
      <p>
        1、服务位置即服务覆盖的省市。
        <br />2、佣金比例即商家与我方签订的商品的总佣金比例。
        <br />3、预估收益即通过商家渠道售卖商品后，商家能得到的预计总收益，具体以实际为准。
      </p>
    </div>
    <div class="soso">
      <div class="keywords">
        <el-input type="text" v-model="keywords" placeholder="请输入服务名称"></el-input>
      </div>
      <el-button type="primary" id="soso" @click="getlist" icon="el-icon-search">搜索</el-button>
      <el-button
        type="success"
        id="soso"
        @click="addservice()"
        icon="el-icon-circle-plus-outline"
      >添加服务</el-button>
    </div>
    <div class="tab">
      <table class="table table-hover table-bordered">
        <thead>
          <tr>
            <th width="10%">服务名称</th>
            <th width="10%">服务提供商</th>
            <th width="20%">服务位置</th>
            <th width="7%">价格</th>
            <th width="10%">佣金比例（%）</th>
            <th width="10%">预估收益（元）</th>
            <th width="8%">审核状态</th>
            <th width="10%">上下架状态</th>
            <th width="15%">操作</th>
          </tr>
        </thead>
        <tbody>
          <template v-if="list.length > 0">
            <tr v-for="item in list" :key="item.id">
              <td>{{item.serviceName}}</td>
              <td>{{item.photoStudioName}}</td>
              <td>{{item.province}}{{item.city}}</td>
              <td v-if="item.price != undefined">{{item.price}}</td>
              <td v-else>-</td>
              <td v-if="item.auditStatus == 1">{{item.commissionPercent}}</td>
              <td v-else>-</td>
              <td
                v-if="item.auditStatus == 1"
              >{{(item.price*(item.commissionPercent)/100).toFixed(2)}}</td>
              <td v-else>-</td>
              <td v-if="item.auditStatus == 0">待审核</td>
              <td v-if="item.auditStatus == 1">审核成功</td>
              <td v-if="item.auditStatus == 2" style="color:#D0021B">审核失败</td>
              <td v-if="item.isOnsell == 0 && item.auditStatus == 1" style="color:#D0021B">已下架</td>
              <td v-if="item.isOnsell == 1 && item.auditStatus == 1">已上架</td>
              <td v-if="item.auditStatus != 1">--</td>
              <td>
                <span v-if="item.isOnsell == 0 && item.auditStatus == 1" @click="onSell(item)">上架</span>
                <span v-if="item.auditStatus == 1" @click="ewm(item)">二维码</span>
                <span
                  @click="delservice(item)"
                  style="color:#D0021B"
                  v-if="item.auditStatus == 0 || item.auditStatus == 2"
                >删除</span>
                <span v-if="item.isOnsell == 1 && item.auditStatus == 1" @click="nosell(item)">下架</span>
              </td>
            </tr>
          </template>
          <tr v-else>
            <td colspan="11">没有相关数据</td>
          </tr>
        </tbody>
      </table>
    </div>
    <el-dialog title="二维码" :visible.sync="dialogVisible" width="30%" center>
      <img :src="ewmimg" v-if="ewmimg" class="ewmimg" />
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "service",
  data() {
    return {
      info: "品牌服务管理",
      keywords: "",
      list: [],
      shopsBrandId: "",
      ewmimg: "",
      dialogVisible: false
    };
  },
  methods: {
    //新增服务
    addservice() {
      this.$router.push({
        name: "addService"
      });
    },
    //获取列表
    getlist() {
      let parmars = {
        shopsBrandId: this.shopsBrandId,
        keywords: this.keywords
      };
      this.$post("/shopsBrand/showAShopsBrandService", parmars).then(res => {
        if (res.error == "00") {
          this.list = res.result.list;
        } else {
          this.$message.error("res.msg");
        }
      });
    },
    //删除服务
    delservice(item) {
      this.$confirm("您确认删除此服务？?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let parmars = {
            id: item.id,
            delFlag: "1"
          };
          this.$post("/shopsBrand/deleteAShopsBrandServiceById", parmars).then(
            res => {
              console.log(res);
              if (res.error == "00") {
                this.$message({
                  type: "success",
                  message: "删除服务成功!"
                });
              } else {
                this.$message.error(res.msg);
              }
              this.getlist();
            }
          );
        })
        .catch(() => {});
    },
    //上架
    onSell(item) {
      this.$confirm("您确定上架此服务？?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let parmars = {
            // shopsBrandId: this.shopsBrandId,
            id: item.id,
            isOnsell: "1"
          };
          this.$post(
            "/shopsBrand/changeShopsBrandServiceIsOnsell",
            parmars
          ).then(res => {
            console.log(res);
            if (res.error == "00") {
              this.$message({
                type: "success",
                message: "服务上架成功"
              });
            } else {
              this.$message.error(res.msg);
            }
            this.getlist();
          });
        })
        .catch(() => {});
    },
    //下架
    nosell(item) {
      this.$confirm("您确定下架此服务？?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let parmars = {
            // shopsBrandId: this.shopsBrandId,
            id: item.id,
            isOnsell: "0"
          };
          this.$post(
            "/shopsBrand/changeShopsBrandServiceIsOnsell",
            parmars
          ).then(res => {
            console.log(res);
            if (res.error == "00") {
              this.$message({
                type: "success",
                message: "服务下架成功"
              });
            } else {
              this.$message.error(res.msg);
            }
            this.getlist();
          });
        })
        .catch(() => {});
    },
    ewm(item) {
      let parmars = {
        page: "pages/clap/clap",
        id:
          "serviceId=" +
          item.serviceId +
          ",relateId=" +
          sessionStorage.getItem("shopsBrandId") +
          ",type=0"
      };
      this.$post("/weixin/getwxTwoEconde", parmars).then(res => {
        if (res.error == "00") {
          this.dialogVisible = true;
          this.ewmimg = res.result;
        } else {
          this.$message.error(res.msg);
        }
      });
    }
  },
  mounted() {
    if (sessionStorage.getItem("shopsBrandId")) {
      this.shopsBrandId = sessionStorage.getItem("shopsBrandId");
      this.getlist();
    }
  }
};
</script>

<style scoped>
.service {
  height: 1000px;
  overflow: auto;
}

.service-title {
  background: #e4e9ef;
  padding: 15px;
  margin: 0 10px;
}
.service-title > div {
  font-size: 20px;
  font-weight: 600;
  color: #4a4a4a;
  margin-bottom: 10px;
}

.service-title > p {
  font-size: 14px;
  font-weight: 400;
  color: rgba(74, 74, 74, 1);
  line-height: 24px;
  padding-left: 18px;
}

.soso {
  width: 100%;
  overflow: hidden;
  margin-bottom: 20px;
  margin-top: 20px;
}

.keywords {
  float: left;
  width: 260px;
  margin-right: 10px;
}

#soso {
  float: left;
  margin-left: 10px;
}

.ewmimg {
  display: block;
  width: 150px;
  height: 150px;
  margin: 20px auto;
}

/*表格样式*/

/*去掉表头边框+背景*/
th {
  border: 1px solid transparent !important;
  background: #e4e9ef;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  color: #5b5b5b;
}

td {
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  color: #5b5b5b;
  vertical-align: middle;
}

td > span {
  margin-left: 14px;
  color: #4a90e2;
  display: none;
  cursor: pointer;
}

tr:hover > td > span {
  display: inline;
}
</style>
